/**
 * @type module
 * @name data-table
 */
.data-table.module-c {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  border-top: 2px solid rgb(226, 230, 233);
  border-bottom: 1px solid rgb(78, 89, 104);
}
.data-table.module-c::-webkit-scrollbar {
  width: 4rem;
  height: 4rem;
}
.data-table.module-c::-webkit-scrollbar-thumb {
  border-radius: 2rem;
  background-color: rgba(0, 0, 0, 0.1);
}
.data-table.module-c::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.data-table.module-c::-webkit-scrollbar-thumb:active {
  background-color: rgba(0, 0, 0, 0.4);
}
.data-table.module-c > table {
  table-layout: fixed;
  width: 100%;
  min-width: 1200rem;
  font-size: 16rem;
  line-height: 24rem;
  border-spacing: 0;
  border-collapse: collapse;
  border-right: hidden;
  border-left: hidden;
}
.data-table.module-c > table caption {
  overflow: hidden;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  line-height: 1px;
  font-size: 1px;
  color: transparent;
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
}
@media (min-width: 1024px) {
  .data-table.module-c > table {
    font-size: 18.6666666667rem;
    line-height: 28rem;
  }
}
.data-table.module-c > table > thead > tr > th {
  padding: 1em 1.5em;
  color: rgb(242, 242, 245);
  font-weight: 700;
  border-right: 1px solid rgb(35, 43, 55);
  border-bottom: 1px solid rgb(78, 89, 104);
  border-left: 1px solid rgb(35, 43, 55);
}
.data-table.module-c > table > tbody > tr > td {
  padding: 1em 1.5em;
  color: rgb(210, 216, 221);
  border-right: 1px solid rgb(35, 43, 55);
  border-left: 1px solid rgb(35, 43, 55);
}
.data-table.module-c > table > tbody > tr:nth-child(even) {
  background-color: rgb(242, 242, 245);
}

