/* tab-display */
.tab-display .tab-list {
  display: flex;
  margin: 0;
  padding: 0;
}
.tab-display .tab-list .tab-item {
  box-sizing: border-box;
  list-style: none;
}
.tab-display .tab-list .tab-item .tab-name {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  text-align: center;
}
.tab-display .tab-list .tab-item.current {
  z-index: 1;
}
.tab-display.type-a .tab-list {
  justify-content: flex-start;
}
.tab-display.type-b .tab-list {
  justify-content: flex-end;
}
.tab-display.type-c .tab-list {
  justify-content: center;
}
.tab-display.type-d .tab-list .tab-item {
  flex: 1 1 100%;
  width: 100%;
}

/*
 * module-c outline
 *
 * @ $line-height
 * @ $font-size
 * @ $font-weight
 * @ $margin-left = $line-height/2 + 2
 */
.tab-display.module-c .tab-list .tab-item {
  display: flex;
  align-items: center;
}
.tab-display.module-c .tab-list .tab-item .tab-name {
  position: relative;
  color: rgb(226, 230, 233);
}
.tab-display.module-c .tab-list .tab-item.current .tab-name {
  color: rgb(0, 0, 0);
}
