  position: relative;
}
.tab-display.module-c.style-a .tab-list::before {
  content: " ";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 100%;
  background-color: rgb(226, 230, 233);
}
.tab-display.module-c.style-a .tab-list .tab-item {
  display: flex;
}
